:root {
  --theme-light: #f8f8f8;
  --theme-dark: #1e1e1e;
  --accent-color: #9bb537;
  --dark-text-title: #000;
  --white-text-title: #fff;
  --dark-text: #050505;
  --input-color-theme-dark: rgba(255, 255, 255, .5);
  --input-color-theme-light: rgba(5, 5, 5, .3);
  --stars-color: #eea10c;
  --recipes-text: rgba(5, 5, 5, .8);
}

@media screen and (max-width: 767px) {
  .header {
    padding-top: 20px;
    display: flex;
  }

  .header-nav, .header-darck-light {
    display: none;
  }

  .header-bth-burger {
    background-color: rgba(0, 0, 0, 0);
    border: none;
  }

  .header-burger-icon {
    stroke: var(--dark-text);
    transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  }

  .header-burger-icon:focus, .header-burger-icon:hover {
    stroke: var(--accent-color);
  }
}

.header-container {
  width: 100%;
